.search-form {
  display: flex;
  justify-content: center;
}

.search-form input {
  width: 50vw;
  padding: 20px;
  border: 2px solid #cccccc;
  outline: 0;
  border-radius: 10px;
  font-size: 50px;
  margin: 20px auto;
}

.search-form input:focus {
  border: 2px solid #a3a3a3;
}
